@import "variables.scss";

@font-face {
  font-family: 'Rawline';
  src: url("../fonts/rawline-300.ttf");
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: 'Rawline';
  src: url("../fonts/rawline-400.ttf");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'Rawline';
  src: url("../fonts/rawline-500.ttf");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: 'Rawline';
  src: url("../fonts/rawline-600.ttf");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: 'Rawline';
  src: url("../fonts/rawline-700.ttf");
  font-weight: 700;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
}


body {
  padding-top: 145px;
  min-width: 320px;

  font-family: $font-family;
  background: $bg;

  &.overfl {
    overflow: hidden;
    height: 100vh;
  }
}

.start-page {
  padding: 30px;

  &-logo {
    display: block;
    margin-bottom: 30px;
  }

  ul {
    display: inline-block;
    vertical-align: top;
    padding-right: 60px;

    li {
      padding: 5px 0;

      b {
        font-size: 20px;
      }

      a {
        font-size: 16px;
        line-height: 140%;
        color: $black;

        &:hover {
          //color: $active;
        }
      }
    }
  }
}

div,
span,
a,
input,
textarea,
select,
button,
.button {
  &:focus {
    outline: none;
  }
}


ul {
  margin-bottom: 0;
  line-height: 0;

  li {
    list-style: none;
  }
}

a {
  cursor: pointer;
  transition: 0.3s;

  &:hover {
    text-decoration: none;
  }
}

svg {
  use {
    transition: 0.3s;
  }
}

.container {
  max-width: 100%;

  @media screen and(min-width: 1350px) {
    max-width: 1320px;
  }
}

.title-main {
  margin-bottom: 50px;

  display: flex;
  align-items: baseline;
  justify-content: space-between;

  h2 {
    width: 80%;
    margin-bottom: 0;
  }

  &__link {
    display: inline-block;
    margin-left: 20px;

    font-size: 13px;
    line-height: 15px;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: $gary2;

    svg {
      width: 22px;
      height: 10px;
      margin-left: 7px;

      use {
        color: $gary2;
      }
    }

    &:hover {
      color: $active-hover;

      svg {
        use {
          color: $active-hover;
        }
      }
    }
  }
}

.button {
  transition: 0.3s;
  box-shadow: none!important;
  border: none;

  cursor: pointer;

  &-tx {
    font-weight: 500;
    font-size: 14px;
    line-height: 150%;
    color: $gray1;

    &:hover {
      color: $active-hover;
    }
  }

  &-primary {
    display: inline-block;
    padding: 14px 26px;

    position: relative;
    background: $active-hover;
    border-radius: 4px;
    overflow: hidden;


    span {
      position: relative;
      z-index: 4;

      font-weight: 600;
      font-size: 16px;
      line-height: 180%;
      color: $white;

      img {
        margin-right: 10px;
      }
    }

    &:before {
      content: '';
      width: 100%;
      height: 100%;
      display: block;

      position: absolute;
      top: 0;
      left: 0;
      z-index: 2;

      border-radius: 4px;
      transition: 0.3s;
      background: linear-gradient(80.47deg, #3989E7 -1.12%, #003878 102.67%);
      opacity: 0;
    }

    &:hover {
      &:before {
        opacity: 1;
      }
    }
  }

  &-bord {
    padding: 14px 26px;
    display: inline-block;

    font-weight: 600;
    font-size: 16px;
    line-height: 180%;
    color: $active-hover;

    border-radius: 4px;
    border: 1px solid $active-hover;

    &:hover {
      background: $active-hover;
      color: $white;
    }
  }

  &-center {
    margin-top: 37px;
    text-align: center;
  }

  &-group {
    .button {
      display: inline-block;
      vertical-align: middle;
      margin: 0 10px;
    }
  }

  &-mob {
    display: none;
  }

  &-ar {
    position: relative;

    width: 193px;
    height: 75px;
    display: block;


    span {
      font-weight: 500;
      font-size: 14px;
      line-height: 150%;
      color: $white;
      transition: 0.3s;
    }

    &:before {
      content: '';
      width: 193px;
      height: 75px;
      display: block;

      position: absolute;
      bottom: 0;
      left: 0;

      background-image: url("../images/svg/arrow-roud.svg");
      background-repeat: no-repeat;
      background-size: 193px 75px;
    }

    &:after {
      content: '';
      width: 246px;
      height: 75px;
      display: block;

      position: absolute;
      bottom: 0;
      left: 0;

      background-image: url("../images/svg/arrow-roud2.svg");
      background-repeat: no-repeat;
      background-size: 246px 75px;
      opacity: 0;
    }

    &:hover {
      &:before {
        opacity: 0;
      }

      &:after {
        opacity: 1;
      }
    }
  }
}

h1 {
  margin-bottom: 30px;

  font-size: 35px;
  line-height: 48px;
  color: $black;
  font-weight: 400;

  i {
    color: $active-hover;
  }
}

h2 {
  margin-bottom: 50px;

  font-size: 35px;
  line-height: 48px;
  color: $black;
  font-weight: 400;

  i {
    color: $active-hover;
  }
}

h3 {
  margin-bottom: 40px;

  font-weight: 500;
  font-size: 30px;
  line-height: 36px;
  color: $black;
}

h4 {
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 24px;
  line-height: 134.9%;
  color: $black;
}

h5 {
  margin-bottom: 20px;

  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 130%;
  color: $black4;
}

.header {
  width: 100%;

  position: fixed;
  top: 0;
  left: 0;

  z-index: 99;

  &__mob {
    display: none;
  }

  &-top {
    padding: 12px 0;

    background: $white;

    &__inner {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &__menu {
      padding: 0 15px;

      ul {
        margin-bottom: 0;

        li {
          padding: 0 11px;
          display: inline-block;
          vertical-align: middle;

          a {
            color: $gary2;
            font-weight: 600;
            font-size: 16px;
            line-height: 180%;

            &:hover {
              color: $active-hover;
            }
          }
        }
      }
    }

    &__right {

    }

    &__inf {
      margin-left: 20px;
      width: 145px;
      display: inline-block;
      vertical-align: middle;

      &-time {
        font-weight: 500;
        font-size: 12px;
        line-height: 130%;
        letter-spacing: 0.02em;
        color: $gary2;
      }

      &-link {
        display: block;

        font-size: 12px;
        line-height: 130%;
        letter-spacing: 0.04em;
        color: $black;

        &:hover {
          color: $active-hover;
        }
      }
    }

    &__tel {
      display: block;

      font-weight: 600;
      font-size: 16px;
      line-height: 180%;
      color: $black3;

      &:hover {
        color: $active-hover;
      }
    }
  }

  &-bottom {
    position: relative;

    background: $blue;

    .header-top__menu,
    .header-top__inf {
      display: none;
    }

    &__inner {
      display: flex;
      align-items: center;
      justify-content: space-between;

      position: relative;
      z-index: 2;
    }
  }

  &__logo {
    display: inline-block;
  }

  &__catalog {
    position: relative;

    &:hover {
      & > .header__catalog-inner {
        display: block;
      }
    }

    &-inner {
      width: 836px;
      display: none;

      padding: 32px 23px 10px;

      position: absolute;
      top: 68px;
      left: 0;
      z-index: 10;

      background: $white;
      border-radius: 4px;

      &:before {
        content: '';
        width: 100%;
        height: 20px;
        display: block;
        position: absolute;
        top: -20px;
        left: 0;
        z-index: 1;
        background: transparent;
      }

      ul {
        display: flex;
        flex-wrap: wrap;

        li {
          margin-bottom: 21px;
          max-width: 33.333%;
          flex: 0 0 33.333%;
          padding: 0 5px;

          a {
            font-size: 14px;
            line-height: 140%;
            color: $black4;

            &:hover {
              color: $active-hover;
            }
          }
        }
      }
    }

    &-title {
      display: flex;
      align-items: center;
      padding: 15px 18px;

      border-radius: 4px;
      border: 2px solid $active;
      transition: 0.3s;

      svg {
        width: 16px;
        height: 16px;

        use {
          color: $active;
        }
      }

      span {
        margin-left: 10px;

        color: $active;
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
        transition: 0.3s;
      }

      &:hover {
        border-color: $active-hover;

        svg {
          use {
            color: $active-hover;
          }
        }

        span {
          color: $active-hover;
        }
      }
    }

  }

  &__search {
    display: inline-block;
    vertical-align: middle;
    margin-left: 32px;

    position: relative;

    &-lnk {
      display: flex;
      align-items: center;
      cursor: pointer;

      span {
        display: inline-block;
        margin-left: 10px;
        font-weight: 500;
        font-size: 14px;
        line-height: 150%;
        color: $white;
        transition: 0.3s;
      }

      svg {
        width: 20px;
        height: 20px;

        use {
          color: $bg;
        }
      }

      &:hover {
        svg {
          use {
            color: $active-hover;
          }
        }

        span {
          color: $active-hover;
        }
      }
    }

    &-in {
      width: 690px;
      display: none;

      position: absolute;
      right: 0;
      bottom: -9px;
      z-index: 10;

      .open & {
        display: block;
      }

      form {
        width: 100%;
      }

      input {
        width: 100%;
        height: 50px;
        padding: 5px 40px 5px 0;

        color: $white;
        font-weight: 500;
        font-size: 16px;
        line-height: 180%;

        border: none;
        border-bottom: 1px solid $bg;
        background: $blue;

        &::-webkit-input-placeholder { color: $white; }
        &:-ms-input-placeholder { color: $white; }
        &::placeholder { color: $white; }


        /*&:focus {
          & + .header__search-product {
            display: block;
          }
        }*/
      }
    }

    &-btn {
      position: absolute;
      left: 0;
      top: 16px;
      z-index: 11;

      background: transparent;

      &.s {
        display: none;
      }

      svg {
        width: 20px;
        height: 20px;

        use {
          color: $gary2;
        }
      }
    }

    &-close {
      position: absolute;
      right: 0;
      top: 16px;
      z-index: 11;

      background: transparent;

      svg {
        width: 15px;
        height: 15px;

        use {
          color: $white;
        }
      }
    }

    &-product {
      width: 678px;
      padding-bottom: 20px;
      display: none;

      position: absolute;
      top: 63px;
      right: 0;
      z-index: 100;

      background: $white;
      border-radius: 0 0 4px 4px;
      box-shadow: 0px 4px 20px rgba(9, 31, 87, 0.1);

      .op & {
        display: block;
      }

      &-item {
        min-height: 110px;
        padding: 19px 17px 19px 168px;

        position: relative;

        border-top: 1px solid $gray16;
      }

      &-lnk {
        display: block;
        padding: 12px 32px;

        text-transform: uppercase;
        font-size: 13px;
        line-height: 15px;
        letter-spacing: 0.02em;
        color: $gary2;

        svg {
          width: 20px;
          height: 10px;
          margin-left: 10px;

          use {
            color: $gary2;
          }
        }

        &:hover {
          color: $active-hover;

          svg {
            use {
              color: $active-hover;
            }
          }
        }
      }

      &-price {
        &-now {
          margin-right: 20px;
          display: inline-block;
          vertical-align: middle;

          font-weight: 500;
          font-size: 24px;
          line-height: 134.9%;
          color: $active-hover;

          &:after {
            display: inline-block;
            margin-left: 3px;
            font-family: "PT Sans", sans-serif;
            content: "\20cf";
          }
        }

        &-old {
          display: inline-block;
          vertical-align: middle;

          font-weight: 500;
          font-size: 16px;
          line-height: 134.9%;
          color: $gray1;
          text-decoration: line-through;

          &:after {
            display: inline-block;
            margin-left: 3px;
            font-family: "PT Sans", sans-serif;
            content: "\20cf";
          }
        }
      }

      &-img {
        width: 140px;
        height: 112px;
        display: block;

        position: absolute;
        top: 0;
        left: 20px;
        z-index: 5;

        img {
          padding: 15px;
          max-height: 110px;
          max-width: 100%;
        }
      }

      &-title {
        display: block;
        margin-bottom: 10px;

        font-weight: 500;
        font-size: 14px;
        line-height: 150%;
        color: $black;

        &:hover {
          color: $active-hover;
        }
      }
    }
  }

  &__cart,
  &__fav,
  &__stat {
    margin-left: 35px;
    display: inline-block;
    vertical-align: middle;

    position: relative;

    &-icon {
      svg {
        width: 24px;
        height: 24px;

        use {
          color: $black;
        }
      }

      &:hover {
        svg {
          use {
            color: $active-hover;
          }
        }
      }
    }

    &-numb {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 21px;
      height: 16px;

      position: absolute;
      bottom: 1px;
      right: -8px;
      z-index: 2;

      font-weight: 600;
      color: $white;
      font-size: 10px;
      line-height: 8px;
      background: $active;
      border: 2px solid $white;
      border-radius: 30px;
      font-family: $font-family2;
    }
  }

  &__menu {
    height: 65px;

    ul {
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;

      li {
        height: 100%;
        position: relative;

        &.cl {
          margin-right: 0!important;
          padding-right: 0;

          & > a {
            padding: 0 26px;
            background: $blue2;
            padding-right: 0;
          }

          & > .drop-bl {
            left: 0!important;
          }

          .arrow {
            display: none;
          }
        }

        & > a {
          height: 100%;
          display: flex;
          align-items: center;
          padding: 0 10px;

          font-weight: 500;
          font-size: 15px;
          line-height: 140%;
          color: $white;
          text-transform: uppercase;

          @media screen and (min-width: 991px){
            &:hover {
              color: $active-hover;
            }
          }
        }

        &.drop {
            & > a {
              padding-right: 20px;
              color: $white2;
              font-weight: 700;

              @media screen and (min-width: 991px){
                &:hover {
                  color: $white;
                }
              }
            }

          .arrow {
            width: 16px;
            height: 16px;

            position: absolute;
            right: 0;
            top: 0;
            bottom: 0;
            margin: auto;

            svg {
              width: 16px;
              height: 17px;

              use {
                color: $white;
              }
            }
          }

          &.cl {
            & > .drop-bl {
              width: 465px;

              li {
                max-width: 50%;
                flex: 0 0 50%;
              }
            }
          }

          & > .drop-bl {
            height: auto;
            width: 283px;
            padding: 25px 23px;
            display: none;
            position: absolute;
            top: 64px;
            left: 0;
            z-index: 50;

            border-radius:  0  0 4px 4px;
            background: $blue;


            &:before {
              content: '';
              width: 100%;
              height: 20px;
              display: block;

              position: absolute;
              top: -20px;
              left: 0;
              z-index: 1;

              background: transparent;
            }

            li {
              height: auto;
              margin-bottom: 21px;
              margin-right: 0;

              &:last-of-type {
                margin-bottom: 0;
              }

              a {
                height: auto;

                text-transform: inherit;
                font-size: 14px;
                line-height: 140%;
                color: $white;
                border: none;

                &:hover {
                  text-decoration: underline;
                }
              }
            }
          }

          @media screen and (min-width: 991px){
            &:hover {
              & > .drop-bl {
                display: block;

              }
            }

            &.cl {
              &:hover {
                & > .drop-bl {
                  display: flex;
                  flex-wrap: wrap;
                }
              }
            }
          }
        }
      }
    }
  }

  &__logo {
    margin-right: 8px;
  }
}

.banner {
  background: $blue3;
  margin-top: -65px;

  overflow: hidden;

  &__slider {

    opacity: 0;
    transition: 0.3s;

    &.slick-initialized {
      opacity: 1;
    }


    &-item {
      padding: 70px 0 18px;

      position: relative;

      background-repeat: no-repeat;
      //background-size: cover;
      background-position: 100% 50%;
      background-image: url("../images/banner-bg.png");

      .tw & {
        padding: 70px 0 70px;
      }
    }

    .button-ar {
      margin: 8px 0 0;
      right: 0;

      @media screen and(min-width: 768px) {
        float: right;
      }

      span {
        padding: 10px 0 0;
        display: inline-block;
      }
    }

    &-img {
      width: 60%;
      height: 100%;

      position: absolute;
      top: 0;
      right: -15%;
      z-index: 3;

      background-repeat: no-repeat;
      background-position: left center;
      //background-size: cover;

      &-mob {
        display: none;
      }
    }

    &-inner {
      width: 608px;
    }

    &-title {
      margin-bottom: 35px;

      font-size: 42px;
      line-height: 120%;
      color: $white;
    }

    &-tx {
      margin-bottom: 40px;

      color: $white;
      font-size: 16px;
      line-height: 160%;
    }

    .button {
      display: inline-block;
      vertical-align: middle;

      &-primary {
        margin-right: 100px;
      }
    }
  }
}

.soc {
  width: 30px;

  position: absolute;
  top: 0;
  left: 0;

  text-align: center;

  ul {
    li {
      margin-bottom: 27px;

      text-align: center;

      a {
        display: block;

        svg {
          width: 24px;
          height: 24px;

          use {
            color: $black4;
          }
        }

        &:hover {
          svg {
            use {
              color: $active-hover;
            }
          }
        }
      }
    }
  }

  &-title {
    margin-top: 3px;
    display: inline-block;

    font-size: 13px;
    line-height: 15px;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: $black4;
    transform: rotate(-180deg);
    writing-mode: vertical-lr;
  }
}

.slick {
  &-dots {
    .banner__slider & {
      position: absolute;
      top: 130px;
      left: 9%;
      z-index: 4;
    }

    li {
      width: 25px;
      height: 25px;
      display: inline-flex;
      align-items: center;
      justify-content: center;

      border: 1px solid transparent;
      border-radius: 100px;

      .banner__slider & {
        margin-bottom: 5px;
        display: flex;
      }

      &.slick-active {
        border-color: $gray1;

        &:before {
          background: $white;
        }
      }

      &:before {
        content: '';
        width: 5px;
        height: 5px;
        display: block;

        background: $gary2;
        border-radius: 100px;
      }

      button {
        display: none;
      }
    }
  }

  &-arrow {
    position: absolute;

    cursor: pointer;

    .news-item__slider.tw & {
      bottom: 20px;
    }

    .news-item__slider.th & {
      bottom: inherit;
      top: 325px;
    }

    .news-item__slider & {
      width: 55px;
      height: 55px;
      display: flex;
      align-items: center;
      justify-content: center;

      bottom: 38px;
      right: 36px;
      z-index: 2;

      background: $white;
      border-radius: 100px;
      transition: 0.3s;

      svg {
        width: 22px;
        height: 10px;

        use {
          color: $gray1;
        }
      }

      &:hover {
        background: $active-hover;

        svg {
          use {
            color: $white;
          }
        }
      }
    }

    .cart-product__slider & {
      bottom: 15px;
      top: inherit;
      z-index: 3;

      svg {
        width: 22px;
        height: 10px;

        use {
          color: $gray1;
        }
      }

      &:hover {
        color: $active-hover;
      }
    }

    .certificates__inner &,
    .clients__inner & {
      top: -48px;
      right: 0;
      z-index: 2;

      svg {
        width: 22px;
        height: 10px;

        use {
          color: $gary2;
        }
      }
    }

    &:hover {
      svg {
        use {
          color: $active-hover;
        }
      }
    }
  }

  &-prev {
    transform: rotate(180deg);

    .certificates__inner &,
    .clients__inner & {
      top: -44px;
      right: 32px;
    }

