    font-weight: 600;
    font-size: 16px;
    line-height: 180%;
    color: $black;
    cursor: pointer;

    border-bottom: 1px solid $gray6;

    .filter__list & {
      padding: 14px 0;
    }

    &:after {
      content: '';
      width: 10px;
      height: 6px;
      display: block;

      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      margin: auto;

      background-repeat: no-repeat;
      background-position: center;
      background-image: url("../images/svg/arrow-filter.svg");
      background-size: 10px 6px;
    }

    &.open {
      &:after {
        transform: rotate(180deg);
      }
    }

  }

  &__range {
    &-item {
      position: absolute;
      top: 81px;

      &:before {
        content: '';
        display: block;

        position: absolute;
        left: 12px;
        top: 8px;

        font-size: 14px;
        line-height: 140%;
        color: $gray1;
        letter-spacing: 0.01em;
      }

      &:nth-of-type(1) {
        left: 0;

        &:before {
          content: 'от';
        }
      }

      &:nth-of-type(2) {
        right: 0;

        &:before {
          content: 'до';
        }
      }
    }

    &.polzunok-container-5 {
      margin: 83px 0 17px;

      box-sizing: border-box;
      outline: none;

      &:after {
        content: "";
        clear: both;
        display: table;
      }

      .ui-slider {
        height: 4px;

        position: relative;

        background: $gray3;
        border-radius: 10px;

        .ui-slider-range {
          height: 4px;

          position: absolute;
          z-index: 1;

          border: none;
          background: $active-hover;
        }
      }

      .ui-slider-handle {
        width: 16px;
        height: 16px;
        margin-left: -6px;

        position: absolute;
        top: -7px;

        cursor: pointer;
        background: $active-hover;
        border: 3px solid $white;
        z-index: 2;

        border-radius: 100px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.05);

        &:focus {
          outline: none;
        }
      }

      .polzunok-input-5-left,
      .polzunok-input-5-right {
        display: inline-block;
        width: 100px;
        height: 37px;
        padding: 10px 10px 10px 40px;

        font-size: 16px;
        line-height: 160%;
        background: $white;
        border: 1px solid $gray3;
        border-radius: 0;
        color: $black2;
        background: $white;

        &:focus {
          border-color: $active;
        }
      }

      .polzunok-input-5-left {
        left: 0;
      }

      .polzunok-input-5-right {
        right: 0;

      }
    }
  }

  &__all {
    span {
      font-weight: 500;
      font-size: 14px;
      line-height: 150%;
      color: $gray1;
      cursor: pointer;

      &:hover {
        color: $active-hover;
      }

      &.show {
        display: block;
      }

      &.hide {
        display: none;
      }
    }

    &.open {
      span {
        &.show {
          display: none;
        }

        &.hide {
          display: block;
          margin-top: 10px;
        }
      }
    }
  }

  &__group {
    &-check {
      position: relative;

      &.open {
        height: 308px;

        overflow: hidden;
      }
    }
  }

  &__check {
    display: none;
    margin-bottom: 6px;

    position: relative;

    &:last-of-type {
      margin-bottom: 0;
    }
    &:nth-of-type(1),
    &:nth-of-type(2),
    &:nth-of-type(3),
    &:nth-of-type(4),
    &:nth-of-type(5),
    &:nth-of-type(6),
    &:nth-of-type(7),
    &:nth-of-type(8) {
      display: block;
    }

    .filter__group-check.open & {
      display: block;
    }

  }

  &__list {
    background: $white;
    border-radius: 4px;
    padding: 7px 20px;

    ul {
      padding-left: 0;
      margin-bottom: 0;

      li {
        margin-bottom: 15px;

        &:last-of-type {
          margin-bottom: 0;
        }

        a {
          font-size: 16px;
          line-height: 180%;
          color: $gary2;

          &:hover {
            color: $active;
          }
        }

        &.active {
          a {
            color: $black;
          }
        }
      }
    }
  }

  &__in {
    background: $white;
    border-radius: 4px;
    padding: 0 20px 30px;
    margin-top: 25px;
  }

  &__btn {
    margin-top: 30px;

    .button-primary {
      height: 40px;
      padding: 0;
      display: flex;
      align-items: center;
      justify-content: center;

      font-weight: 600;
      font-size: 16px;
      line-height: 180%;
    }
  }
}

input {
  &[type=checkbox],
  &[type=radio] {
    display: none;

    & + label {
      padding-left: 25px;

      font-size: 16px;
      line-height: 160%;
      letter-spacing: 0.01em;
      color: $black2;
      cursor: pointer;
      transition: 0.3s;

      a {
        color: $active;

        &:hover {
          color: $black2;
        }
      }

      &:before {
        content: "";
        width: 16px;
        height: 16px;
        display: inline-block;
        margin: -5px 5px 0 0;

        position: absolute;
        top: 11px;
        left: 0;

        border: 1px solid $gray3;
        background-color: transparent;
        text-align: center;
        vertical-align: middle;
        transition: color ease .3s;
        border-radius: 0;
      }

      &:hover {
        color: $active-hover;
      }
    }
  }

  &:checked  {
    & + label {
      &:before {
        background-color: $active-hover;
        background-image: url("../images/svg/check.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: 12px 12px;
        border-color: $active-hover;
      }
    }
  }
}

