    }
  }

  &-page {
    &__inner {

    }

    &__holder {
      &.services {
        margin: 0;
      }

      padding-bottom: 40px;

       .title-main {
         margin-bottom: 0;

         h1 {
           width: calc(100% - 200px);
         }
       }
    }

    &__item {
      display: flex;
      margin-bottom: 20px;

      background: $white;

      border-radius: 4px;
      overflow: hidden;

      &:hover {
        .object-page__item-title {
          color: $active-hover;
        }

        .object-page__item-arrow {
          svg {
            opacity: 0;
          }

          &:before {
            opacity: 1;
          }
        }
      }

      &-img {
        width: 273px;
        min-height: 310px;

        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
      }

      &-list {
        display: block;
        width: calc(100% - 273px);
        padding: 30px;

        position: relative;
      }

      &-title {
        margin-bottom: 20px;
        display: block;

        font-weight: 600;
        font-size: 18px;
        line-height: 130%;
        color: $black;
        transition: 0.3s;
      }

      &-tx {
        margin-bottom: 13px;
        display: block;

        font-size: 14px;
        line-height: 140%;
        color: $gary2;
      }

      &-arrow {
        position: absolute;
        left: 30px;
        bottom: 10px;
        z-index: 2;

        svg {
          width: 22px;
          height: 10px;

          use {
            color: $gary2;
          }
        }

        &:before {
          content: '';
          width: 48px;
          height: 8px;
          display: block;

          position: absolute;
          top: 10px;
          left: 0;
          z-index: 3;

          background-image: url("../images/svg/arrow-hov.svg");
          background-repeat: no-repeat;
          background-size: 48px 8px;
          opacity: 0;
          transition: 0.3s;
        }
      }
    }
  }
}

