.modal {
  &-dialog {
    max-width: 703px;
    border-radius: 4px;
    border: none;
    overflow: hidden;

    .call & {
      max-width: 860px;
    }
  }

  &-content {
    padding: 80px 20px;

    border-radius: 0;

    position: relative;

    background-color: $blue3;
    background-image: url("../images/modal-bg.png");
    //background-size: cover;
    background-position: center;

    .close {
      opacity: 1;

      position: absolute;
      top: 25px;
      right: 30px;
      z-index: 5;

      svg {
        width: 16px;
        height: 16px;

        use {
          color: $white;
        }
      }
    }
  }

  &__text {
    text-align: center;

    .rev & {
      text-align: left;
    }

    &.th {
      width: 80%;
      margin: auto;
    }

    .rating {
      width: 165px;
      height: 40px;
      position: relative;
      margin-bottom: 20px;
    }

    .clear {
      clear: both;
    }

    .rating > input { display: none; }
    .rating > label:before {
      margin: 5px;
      font-size: 40px;
      display: inline-block;
      content: "\2605";
    }

    .rating > .half:before {
      content: "\2605";
      position: absolute;
    }

    .rating > label {
      margin-right: 8px;
      color: #A9ABBD;
      float: right;
      position: relative;

      &:before {
        top: 0;
        border: none;
        background: transparent!important;
      }
    }

    .rating > input:checked ~ label,
    .rating:not(:checked) > label:hover,
    .rating:not(:checked) > label:hover ~ label { color: #3693FF;  }

    .rating > input:checked + label:hover,
    .rating > input:checked ~ label:hover,
    .rating > label:hover ~ input:checked ~ label,
    .rating > input:checked ~ label:hover ~ label { color: #3693FF;  }

    &-call {
      .modal__text-title {
        text-align: left;
        margin-bottom: 50px;
      }
    }

    .form {
      &__holder {
        margin: 0 auto;
      }

      &__item {
        &-tx {
          text-align: left;
        }
      }
    }

    &-title {
      margin-bottom: 20px;

      font-size: 35px;
      line-height: 36px;
      color: $white;
    }

    p {
      font-size: 18px;
      line-height: 180%;
      color: $white;
    }

    .button-group {
      margin-top: 30px;
    }

  }

  &__foto {
    margin-top: 10px;

    &-item {
      width: 89px;
      height: 89px;
      margin-right: 15px;
      display: inline-block;
      vertical-align: middle;
      margin-bottom: 5px;

      position: relative;
      border-radius: 4px;
      overflow: hidden;

      img {
        max-width: 100%;
        max-height: 89px;
      }
    }

    &-cl {
      position: absolute;
      top: 0;
      right: 5px;
      z-index: 2;

      cursor: pointer;
    }

    &-title {
      margin-bottom: 20px;

      font-weight: 500;
      font-size: 13px;
      line-height: 16px;
      /* identical to box height */

      letter-spacing: 0.05em;
      text-transform: uppercase;
      color: $white;
    }

    &-inner {

    }

    &-text {
      display: inline-block;
      vertical-align: middle;

      .t {
        display: block;
        margin-bottom: 7px;

        font-size: 14px;
        line-height: 140%;
        color: $white;
      }

      .tt {
        display: block;

        font-weight: 500;
        font-size: 12px;
        line-height: 166%;
        color: $gary2;
      }
    }

    &-prev {
      width: 89px;
      height: 89px;
      display: inline-block;
      vertical-align: middle;
      margin-right: 15px;

      border-radius: 4px;

      background-color: $gray1;
      background-image: url("../images/svg/add.svg");
      background-repeat: no-repeat;
      background-size: 30px 30px;
      background-position: center;
    }
  }

  &__rt {
    text-align: right;
    margin-top: 10px;
  }
}

.comparison {

  &__inner {
    display: flex;
  }

  &__descr {
    width: 310px;

    &-category {
      position: relative;

      &-drop {
        width: 100%;
        display: none;

        position: absolute;
        top: 50px;
        left: 0;
        z-index: 4;

        padding: 13px 40px 13px 20px;
        margin-top: 15px;
        background: $white;
        border-radius: 4px;

        .open & {
          display: block;
        }

        ul {
          li {
            margin-bottom: 10px;

            &:last-of-type {
              margin-bottom: 0;
            }

            a {
              font-weight: 600;
              font-size: 16px;
              line-height: 180%;
              color: $black;

              &:hover {
                color: $active;
              }
            }
          }
        }
      }

      &-title {
        display: block;
        padding: 13px 40px 13px 20px;

        position: relative;

        font-weight: 600;
        font-size: 16px;
        line-height: 180%;
        color: $black;
        background: $white;
        border-radius: 4px;
        cursor: pointer;

        svg {
          width: 20px;
          height: 20px;

          position: absolute;
          right: 10px;
          top: 0;
          bottom: 0;
          margin: auto;
          z-index: 2;


          use {
            color: $black;
          }
        }

        .open & {
          svg {
            transform: rotate(180deg);
          }
        }
      }


    }

    &-back {
      margin-bottom: 32px;
      display: inline-block;
      padding-left: 20px;

      position: relative;

      font-size: 13px;
      line-height: 130%;
      color: $black;

      svg {
        width: 20px;
        height: 20px;

        position: absolute;
        left: 0;
        top: 0;
        z-index: 2;

        transform: rotate(90deg);

        use {
          color: $black;
        }
      }

      &:hover {
        color: $active-hover;

        svg {
          use {
            color: $active-hover;
          }
        }
      }
    }
  }

  &__tbl {
    width: 100%;
    margin-top: 288px;

    &-item {
      height: 70px;
      padding: 20px;
      display: flex;
      align-items: flex-end;

      border-bottom: 1px solid $gray3;
      font-size: 13px;
      line-height: 15px;
      font-weight: 700;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      color: $black;
    }
  }

  &__slider {
    width: calc(100% - 310px);
    padding-left: 20px;

    .slick {
      &-slide {
        padding: 0 10px;
      }


      &-prev {
        left: 8px;
      }

      &-next {
        right: -10px;
      }

      &-arrow {
        width: 40px;
        height: 40px;
        display: flex!important;
        align-items: center;
        justify-content: center;

        position: absolute;
        top: 150px;
        z-index: 5;

        background: $active-hover;
        border-radius: 100px;

        svg {
          width: 20px;
          height: 20px;

          use {
            color: $white;
          }
        }
      }

      &-disabled {
        display: none!important;
      }
    }

    .catalog__item {
      height: 100%;

      &:hover {
        box-shadow: none;
      }

      &-stat {
        svg {
          width: 17px;
          height: 17px;
        }
      }

      &-bottom {
        display: none;
      }

      &-tbl {
        margin-top: 23px;

        &-item {
          height: 70px;
          padding: 20px 0;
          display: flex;
          align-items: flex-end;

          font-weight: 500;
          font-size: 14px;
          line-height: 150%;
          color: $black;

          border-top: 1px solid $gray3;

          &:first-of-type {
            border-top: none;
          }
        }
      }
    }
  }
}

